import React from 'react';
import s from './style.module.scss';

export const Modal = ({ handleClose, currentProject }) => {
  return (
    <div className={s.modal} onClick={handleClose}>
      <div className={s.container} onClick={(e) => e.stopPropagation()}>
        <h2>{currentProject?.title}</h2>
        <h3>
          <b>Libraries: </b>
          {currentProject.lib}
        </h3>
        <h3>
          <b>Description: </b>
          {currentProject.desc}
        </h3>
        <h3>
          <b>Link: </b>
          <a href={currentProject.link} target='_blank' rel='noreferrer'>
            {currentProject.link || 'This project is local'}
          </a>
        </h3>
        {currentProject.video && (
          <video controls src={currentProject.video}></video>
        )}
        <button onClick={handleClose}>X</button>
      </div>
    </div>
  );
};
