import React from "react";
import { Link } from "react-router-dom";
import { ReactComponent as InfoIcon } from "../../assets/icons/info_icon.svg";
import kentronLogo from "../../assets/portfolio/kentron_logo.svg";
import araratLogo from "../../assets/portfolio/ararat_logo.svg";
import bcLogo from "../../assets/portfolio/bc_logo.svg";
import busyLogo from "../../assets/portfolio/busy_logo.svg";
import chatLogo from "../../assets/portfolio/chat_logo.svg";
import inWaysLogo from "../../assets/portfolio/innovativeways_logo.svg";
import meamealLogo from "../../assets/portfolio/meameal_logo.svg";
import PinkBerryLogo from "../../assets/portfolio/pinkBerry_logo.svg";
import tutorPlaceLogo from "../../assets/portfolio/tutorPlace_logo.svg";
import willSkillLogo from "../../assets/portfolio/willskill_logo.svg";
import bcDdemo from "../../assets/video/bc_demo.mp4";
import chatDemo from "../../assets/video/chat_demo.mp4";
// import pinkDemo from "../../assets/video/pink_demo.mp4";
import { Modal } from "./modal";
import s from "./style.module.scss";
import { motion, useDragControls } from "framer-motion";

const projects = [
  {
    title: "Real Estate",
    link: "https://www.real-estate.am",
    img: kentronLogo,
    lib: "Next.js (+TypeScript)",
    desc: "The website of the real estate agency is one of the leading websites in Armenia, the website is multilingual.",
  },
  {
    title: "Ararat Reality",
    link: "https://ararat-realty.com",
    img: araratLogo,
    lib: "Next.js (+TypeScript)",
    desc: "The website of the real estate agency is one of the leading websites in Armenia, the website is multilingual.",
  },
  {
    title: "Innovative Ways",
    link: "https://innovativeways.net",
    img: inWaysLogo,
    lib: "Next.js (+TypeScript)",
    desc: "A site for logistics services for shipping in the USA, the site has been up and running for over 2 year and is under maintenance.",
  },
  {
    title: "WillSkill",
    link: "https://willskill.ru",
    img: willSkillLogo,
    lib: "Next.js (+TypeScript)",
    desc: "People post their video lessons on the site, the site is multilingual but focused on the Russian market, it has been operating for several years.",
  },
  {
    title: "TutorPlace",
    link: "https://tutorplace.ru",
    img: tutorPlaceLogo,
    lib: "Pug.js",
    desc: "People post their video lessons on the site, the site is multilingual but focused on the Russian market, it has been operating for several years.",
  },
  {
    title: "Busy",
    link: "https://busy.am",
    img: busyLogo,
    lib: "React.js",
    desc: "A reservation website where you can reserve a table and order before you arrive at the restaurant, the website is designed for Armenia.",
  },
  {
    title: "MeaMeal",
    link: "https://meameal.com",
    img: meamealLogo,
    lib: "React.js",
    desc: "The Site is designed for ordering, delivery, or pickup from various restaurants in the United States",
  },
  {
    title: "Business Control",
    img: bcLogo,
    lib: "React.js",
    desc: "A business automation program consisting of various and complexly structured pages, the program is installed at the client",
    video: bcDdemo,
  },
  {
    title: "Pink Berry",
    img: PinkBerryLogo,
    lib: "Next.js (+TypeScript)",
    desc: "Employee payroll automation software, the software is installed at the client",
  },
  {
    title: "Chat",
    img: chatLogo,
    lib: "Front: React.js, Socket | Back: Express.js, Socket | Database: MongoDB",
    desc: "Full stack online live chat with the ability to send pictures, delete and edit sent messages",
    video: chatDemo,
  },
];

const container = {
  hidden: {},
  visible: {
    transition: {
      delayChildren: 0.3,
      staggerChildren: 0.3,
    },
  },
};

const item = {
  hidden: { y: 20, opacity: 0, scale: 0 },
  visible: {
    y: 0,
    opacity: 1,
    scale: 1,
    transition: {
      type: "spring",
      stiffness: 100,
      damping: 20,
    },
  },
};

const Portfolio = () => {
  const [isOpen, setIsOpen] = React.useState(false);
  const [currentProject, setCurrentProject] = React.useState(null);
  const controls = useDragControls();

  const handleOpen = (project) => {
    setIsOpen(true);
    setCurrentProject(project);
  };

  const handleClose = () => {
    setIsOpen(false);
  };

  return (
    <>
      <motion.div
        className={s.container}
        variants={container}
        initial="hidden"
        animate="visible"
      >
        {projects.map((project, i) => (
          <motion.div
            drag
            dragControls={controls}
            variants={item}
            title={project?.title}
            key={i}
            className={s.itemBlock}
          >
            <img src={project.img} alt={project?.title} />
            <Link to={project.link} data-local={!project.link} target="_blank">
              {project.link ? "Live" : "Local"}
            </Link>
            <button
              title="Information"
              className={s.info}
              onClick={() => handleOpen(project)}
            >
              <InfoIcon />
            </button>
          </motion.div>
        ))}
      </motion.div>
      {isOpen && (
        <Modal handleClose={handleClose} currentProject={currentProject} />
      )}
    </>
  );
};

export default Portfolio;
